import { PureComponent } from 'react';

import Wrapper from 'Component/Wrapper';
import { _ } from 'Util/TransformBem';
import './DatabaseCard.style.scss';
import PropTypes from 'prop-types';

class DatabaseCardComponent extends PureComponent {
  static propTypes = {
    id: PropTypes.string,
    explosions: PropTypes.array.isRequired,
    isTitle: PropTypes.bool,
    noHeight: PropTypes.bool
  };

  renderParam(param) {
    const { name, value } = param;

    return (
      <div {..._({ block: 'DatabaseCard', elem: 'Param' })}>
        <span {..._({ block: 'DatabaseCard', elem: 'ParamTitle' })}>
          {name}
        </span>
        <span {..._({ block: 'DatabaseCard', elem: 'ParamValue' })}>
          {value}
        </span>
      </div>
    );
  }

  renderItem(item) {
    const { id, code_name, operation, event, params, img } = item;

    return (
      <div key={id} {..._({ block: 'DatabaseCard', elem: 'Item' })}>
        <img src={img} {..._({ block: 'DatabaseCard', elem: 'Image' })} />
        <div {..._({ block: 'DatabaseCard', elem: 'ItemContent' })}>
          <span {..._({ block: 'DatabaseCard', elem: 'Title' })}>
            {code_name}
          </span>
          <span {..._({ block: 'DatabaseCard', elem: 'Title' })}>
            {`Операція ${operation}`}
          </span>
          {event && (
            <span {..._({ block: 'DatabaseCard', elem: 'Title' })}>
              {`Подія ${event}`}
            </span>
          )}
          <div {..._({ block: 'DatabaseCard', elem: 'Params' })}>
            {params.map(this.renderParam.bind(this))}
          </div>
        </div>
      </div>
    );
  }

  renderItems() {
    const { explosions, isTitle } = this.props;

    return (
      <>
        {isTitle && (
          <h2 {..._({ block: 'DatabaseCard', elem: 'Header' })}>Вибухи</h2>
        )}
        <div {..._({ block: 'DatabaseCard', elem: 'Items' })}>
          {explosions.map(this.renderItem.bind(this))}
        </div>
      </>
    );
  }

  render() {
    const { id = 'DatabaseCard', noHeight } = this.props;

    return (
      <div id={id} {..._({ block: 'DatabaseCard', mods: { noHeight } })}>
        <Wrapper>{this.renderItems()}</Wrapper>
      </div>
    );
  }
}

export default DatabaseCardComponent;
