/* eslint-disable react/prop-types */
import { PureComponent } from 'react';

import {
  Button,
  LinearProgress,
  ToggleButton,
  ToggleButtonGroup
} from '@mui/material';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import DatabaseCard from 'Component/DatabaseCard';
import Wrapper from 'Component/Wrapper';
import { Field, Form, Formik } from 'formik';
import { TextField } from 'formik-mui';
import { Interweave } from 'interweave';
import PropTypes from 'prop-types';
import { Result } from 'Type/Result';
import { _ } from 'Util/TransformBem';
import { BERELY, NONE, PLUTON, URAN } from './CalculatorCard.config';
import './CalculatorCard.style.scss';

const CustomizedSelectForFormik = ({ children, form, field, disabled }) => {
  const { name, value } = field;
  const { setFieldValue } = form;

  return (
    <Select
      name={name}
      value={value}
      onChange={e => {
        setFieldValue(name, e.target.value);
      }}
      disabled={disabled}
    >
      {children}
    </Select>
  );
};

class CalculatorCardComponent extends PureComponent {
  static propTypes = {
    validateForm: PropTypes.func.isRequired,
    handleSubmit: PropTypes.func.isRequired,
    handleTypeSelect: PropTypes.func.isRequired,
    type: PropTypes.oneOf([URAN, PLUTON]),
    result: PropTypes.instanceOf(Result),
    isLoading: PropTypes.bool.isRequired,
    error: PropTypes.string.isRequired
  };

  renderInputTitle(title) {
    return <h3 {..._({ block: 'CalculatorCard', elem: 'Title' })}>{title}</h3>;
  }

  renderForm() {
    const { handleSubmit, type, handleTypeSelect, validateForm, isLoading } =
      this.props;
    return (
      <div
        {..._({ block: 'CalculatorCard', elem: 'Inputs' })}
        data-aos='fade-zoom-in'
        data-aos-easing='ease-in-back'
      >
        <Formik
          initialValues={{
            temperMaterial: NONE
          }}
          validate={validateForm}
          onSubmit={async (values, { setSubmitting }) => {
            await handleSubmit(values);
            setSubmitting(false);
          }}
        >
          {({ submitForm, isSubmitting }) => (
            <Form>
              <div {..._({ block: 'CalculatorCard', elem: 'Input' })}>
                {this.renderInputTitle('Тип матеріалу')}
                <div {..._({ block: 'CalculatorCard', elem: 'InputRow' })}>
                  <ToggleButtonGroup
                    exclusive
                    value={type}
                    onChange={handleTypeSelect}
                  >
                    <ToggleButton value={URAN}>
                      <span>Уран-238</span>
                    </ToggleButton>
                    <ToggleButton value={PLUTON}>
                      <span>Плутоній</span>
                    </ToggleButton>
                  </ToggleButtonGroup>
                </div>
              </div>

              <div {..._({ block: 'CalculatorCard', elem: 'Input' })}>
                {this.renderInputTitle('Кількість матеріалу')}
                <div {..._({ block: 'CalculatorCard', elem: 'InputRow' })}>
                  <Field
                    component={TextField}
                    label={'Кількість (кг)'}
                    name={'amountMaterial'}
                    type='number'
                    sx={{ width: '50%' }}
                  />
                  {type === URAN && (
                    <Field
                      component={TextField}
                      label={'Чистота (%)'}
                      name={'purity'}
                      type='number'
                      sx={{ width: '50%' }}
                    />
                  )}
                </div>
              </div>

              <div {..._({ block: 'CalculatorCard', elem: 'Input' })}>
                {this.renderInputTitle('Наявність темпера')}
                <div {..._({ block: 'CalculatorCard', elem: 'InputRow' })}>
                  <FormControl sx={{ width: '100%' }}>
                    <InputLabel>Тип</InputLabel>
                    <Field
                      name={'temperMaterial'}
                      component={CustomizedSelectForFormik}
                    >
                      <MenuItem value={NONE}>Немає</MenuItem>
                      <MenuItem value={URAN}>Уран-235</MenuItem>
                      <MenuItem value={BERELY}>Берилій</MenuItem>
                    </Field>
                  </FormControl>
                </div>
              </div>

              <div {..._({ block: 'CalculatorCard', elem: 'Input' })}>
                {this.renderInputTitle('Початкова кількість нейтронів')}
                <Field
                  component={TextField}
                  label={'Кількість атомів на початку реакції'}
                  name={'numberOfNeutrons'}
                  type='number'
                />
              </div>

              {isLoading && <LinearProgress />}

              <Button
                variant='contained'
                color='primary'
                disabled={isSubmitting || isLoading}
                onClick={submitForm}
              >
                Рассчитать
              </Button>
            </Form>
          )}
        </Formik>
      </div>
    );
  }

  renderResultInfo() {
    const { result } = this.props;

    if (!result) {
      return null;
    }

    const {
      img,
      materialReacted,
      yield: maskedYield,
      additionalParameters
    } = result;

    return (
      <>
        <img
          src={img}
          {..._({ block: 'CalculatorCard', elem: 'ResultImage' })}
        />
        <div {..._({ block: 'CalculatorCard', elem: 'ResultInfo' })}>
          <div {..._({ block: 'CalculatorCard', elem: 'ResultInfoRow' })}>
            <span>Потужність</span>
            <span>{maskedYield} кт</span>
          </div>
          <div {..._({ block: 'CalculatorCard', elem: 'ResultInfoRow' })}>
            <span>Кількість зреагованого матеріалу:</span>
            <span>
              {materialReacted > 1000
                ? `${materialReacted / 1000} кг`
                : `${materialReacted} г`}
            </span>
          </div>
          {additionalParameters.map(({ name, value }) => (
            <div
              key={name}
              {..._({ block: 'CalculatorCard', elem: 'ResultInfoRow' })}
            >
              <span>{name}</span>
              <span>{value}</span>
            </div>
          ))}
        </div>
      </>
    );
  }

  renderResultCard() {
    const { isLoading, result } = this.props;

    return (
      <div
        {..._({
          block: 'CalculatorCard',
          elem: 'Result',
          mods: { noResult: !result }
        })}
      >
        <div
          {..._({
            block: 'CalculatorCard',
            elem: 'ResultCard',
            mods: { isLoading: isLoading || !result }
          })}
        >
          {!isLoading && this.renderResultInfo()}
        </div>
      </div>
    );
  }

  renderItems() {
    const { result } = this.props;

    if (!result) {
      return null;
    }

    const { examplesList } = result;

    return (
      <>
        <h2 {..._({ block: 'CalculatorCard', elem: 'RelatedExplosions' })}>
          Схожі вибухи
        </h2>
        <DatabaseCard
          id='related-explosions'
          explosions={examplesList}
          noHeight
        />
      </>
    );
  }

  renderAdditions() {
    const { result } = this.props;

    if (!result) {
      return null;
    }

    const { suggestions, explanations } = result;

    return (
      <div>
        <div {..._({ block: 'CalculatorCard', elem: 'Suggestions' })}>
          <div {..._({ block: 'CalculatorCard', elem: 'Suggestions-title' })}>
            Пояснення
          </div>
          <Interweave
            {..._({ block: 'CalculatorCard', elem: 'Suggestions-desc' })}
            content={explanations}
          />
        </div>
        <div {..._({ block: 'CalculatorCard', elem: 'Suggestions' })}>
          <div {..._({ block: 'CalculatorCard', elem: 'Suggestions-title' })}>
            Пропозиція
          </div>
          <Interweave
            {..._({ block: 'CalculatorCard', elem: 'Suggestions-desc' })}
            content={suggestions}
          />
        </div>
      </div>
    );
  }

  render() {
    return (
      <div {..._({ block: 'CalculatorCard' })}>
        <Wrapper>
          <div {..._({ block: 'CalculatorCard', elem: 'Calculator' })}>
            {this.renderForm()}
            {this.renderResultCard()}
          </div>
          {this.renderAdditions()}
          {this.renderItems()}
        </Wrapper>
      </div>
    );
  }
}

export default CalculatorCardComponent;
