import React, { PureComponent } from 'react';

import './MenuCard.style.scss';
import { _ } from 'Util/TransformBem';
import MenuItem from 'Component/MenuItem';

import CalculationIcon from 'Assets/calculation.png';
import ExplosionIcon from 'Assets/explosion.png';

class MenuCardComponent extends PureComponent {
  items = [
    // {
    //   icon: TheoryIcon,
    //   name: 'Теория',
    //   description: 'Базовая инфа',
    //   to: 'TheoryCard',
    //   delay: 0
    // },
    {
      icon: CalculationIcon,
      name: 'Розрахунок',
      description: 'Калькулятор',
      to: 'CalculatorCard',
      delay: 0
    },
    {
      icon: ExplosionIcon,
      name: 'Вибухи',
      description: 'База даних вибухів',
      to: 'DatabaseCard',
      delay: 200
    }
  ];

  renderItem(item) {
    return <MenuItem {...item} />;
  }

  renderItems() {
    return (
      <div {..._({ block: 'MenuCard', elem: 'Items' })}>
        {this.items.map(this.renderItem.bind(this))}
      </div>
    );
  }

  render() {
    return (
      <div id='MenuCard' {..._({ block: 'MenuCard' })}>
        {this.renderItems()}
      </div>
    );
  }
}

export default MenuCardComponent;
