import PropTypes from 'prop-types';

import { PureComponent } from 'react';
import { Link } from 'react-scroll';
import { _ } from 'Util/TransformBem';

import './MenuItem.style.scss';

class MenuItemComponent extends PureComponent {
  static propTypes = {
    icon: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    to: PropTypes.string.isRequired,
    delay: PropTypes.number.isRequired
  };

  render() {
    const { icon, name, description, to, delay } = this.props;

    return (
      <Link to={to} smooth={true} duration={500}>
        <div
          data-aos='fade-zoom-in'
          data-aos-easing='ease-in-back'
          data-aos-delay={delay}
        >
          <div {..._({ block: 'MenuItem' })}>
            <img src={icon} {..._({ block: 'MenuItem', elem: 'Icon' })} />
            <span {..._({ block: 'MenuItem', elem: 'Name' })}>{name}</span>
            <span {..._({ block: 'MenuItem', elem: 'Description' })}>
              {description}
            </span>
          </div>
        </div>
      </Link>
    );
  }
}

export default MenuItemComponent;
