import { PureComponent } from 'react';

import './AboutCard.style.scss';
import { _ } from 'Util/TransformBem';
import { Button } from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import Wrapper from 'Component/Wrapper';
import BackgroundVideo from 'Assets/background.mp4';
import { Link } from 'react-scroll';

class AboutCardComponent extends PureComponent {
  renderBackground() {
    return (
      <video
        autoPlay
        muted
        loop
        {..._({ block: 'AboutCard', elem: 'Background' })}
      >
        <source src={BackgroundVideo} type='video/mp4' />
      </video>
    );
  }

  renderInfo() {
    return (
      <div {..._({ block: 'AboutCard', elem: 'Info' })}>
        <Wrapper>
          <h1 {..._({ block: 'AboutCard', elem: 'Title' })}>Atomic ES</h1>
          <h3 {..._({ block: 'AboutCard', elem: 'Description' })}>
            Більшість публічних описів деталей конструкції ядерної зброї
            ґрунтуються на припущеннях, &quot;зворотному проектуванні&quot; на
            основі відомої інформації або порівняннях з анологічними областями
            фізики.
          </h3>
          <Link to='MenuCard' smooth={true} offset={50} duration={500}>
            <Button
              variant='contained'
              endIcon={<SendIcon />}
              {..._({ block: 'AboutCard', elem: 'Menu' })}
            >
              Меню
            </Button>
          </Link>
        </Wrapper>
      </div>
    );
  }

  render() {
    return (
      <div {..._({ block: 'AboutCard' })}>
        {this.renderBackground()}
        {this.renderInfo()}
      </div>
    );
  }
}

export default AboutCardComponent;
