import {
  CLOUD_DIAMETR,
  CLOUD_HEIGHT,
  DIAMETR_VOGN_KULI,
  MAX_LIGHT,
  MUSHROOM_HEIGHT,
  POWER,
  TIME_OF_LIGHT
} from 'Component/DatabaseCard/explosions_data';
import calculatorService from 'Query/calculator.service';
import { PureComponent } from 'react';
import CalculatorCard from './CalculatorCard.component';
import { PLUTON, URAN } from './CalculatorCard.config';

class CalculatorCardContainer extends PureComponent {
  state = {
    type: URAN,
    amountMaterial: 0,
    result: null,
    isLoading: false,
    error: null
  };

  containerFunctions = {
    validateForm: this.validateForm.bind(this),
    handleSubmit: this.handleSubmit.bind(this),
    handleTypeSelect: this.handleTypeSelect.bind(this)
  };

  containerProps() {
    const { type, result, isLoading } = this.state;

    return {
      type,
      result,
      isLoading
    };
  }

  handleTypeSelect(_e, type) {
    this.setState({ type });
  }

  async handleSubmit(values) {
    const coreMaterial = this.state.type;
    const { type } = this.state;
    const { purity, amountMaterial, temperMaterial, numberOfNeutrons } = values;

    this.setState({ isLoading: true });

    try {
      const result = await calculatorService.calculate(
        type == PLUTON ? 100 : purity,
        amountMaterial,
        coreMaterial,
        temperMaterial,
        numberOfNeutrons
      );

      const {
        examplesList,
        additionalParameters: { nuclearImg } = {},
        additionalParameters
      } = result;

      const translatedParametrsMap = {
        diameter: DIAMETR_VOGN_KULI,
        maxLight: MAX_LIGHT,
        timeLit: TIME_OF_LIGHT,
        mushroomHeight: MUSHROOM_HEIGHT,
        cloudHeight: CLOUD_HEIGHT,
        cloudDiameter: CLOUD_DIAMETR
      };

      const transformedParametrs = Object.entries(additionalParameters)
        .filter(([name]) => name !== 'fireImg' && name !== 'nuclearImg')
        .map(([name, value]) => {
          return {
            name: translatedParametrsMap[name],
            value
          };
        });

      const transformedList = examplesList.map(item => {
        const { power, device, country, test_site, date, test_type, url } =
          item;
        const params = [
          { name: POWER, value: `${power} кт` },
          { name: 'Девайс', value: device },
          { name: 'Країна', value: country },
          { name: 'Дата', value: date },
          { name: 'Майданчик', value: test_site },
          { name: 'Тестування', value: test_type }
        ];
        return {
          ...item,
          img: url,
          params
        };
      });

      const newResult = {
        ...result,
        img: nuclearImg,
        examplesList: transformedList,
        additionalParameters: transformedParametrs
      };

      this.setState({ result: newResult, isLoading: false });
    } catch (e) {
      this.setState({ error: e.message });
      this.setState({ isLoading: false });
    }
  }

  validateForm(values) {
    const type = this.state.type;

    const errors = {};
    if (type === URAN && Number.isInteger(values.purity)) {
      if (values.purity > 100) {
        errors.purity = 'Не може бути більше 100';
      } else if (values.purity < 50) {
        errors.purity = 'Не може бути менше 50';
      }
    }

    return errors;
  }

  render() {
    return (
      <CalculatorCard {...this.containerFunctions} {...this.containerProps()} />
    );
  }
}

export default CalculatorCardContainer;
