import axios from 'axios';
import { SERVER_API } from '.';

class CalculatorService {
  /**
   * @param {number} purity
   * @param {float} amountMaterial
   * @param {('Uranium' | 'Plutonium')} coreMaterial
   * @param {('None'|'Uranium'|'Beryllium')} temperMaterial
   * @param {number} numberOfNeutrons
   */
  async calculate(
    purity,
    amountMaterial,
    coreMaterial,
    temperMaterial,
    numberOfNeutrons
  ) {
    const {
      data: { data }
    } = await axios.get(`${SERVER_API}/calculate`, {
      params: {
        purity,
        amountMaterial,
        coreMaterial,
        temperMaterial,
        numberOfNeutrons
      }
    });

    // data.additionalParameters = new Map(
    //   Object.entries(data.additionalParameters)
    // );
    return data;
  }
}

export default new CalculatorService();
