import { PureComponent } from 'react';
import CalculatorCard from 'Component/CalculatorCard';
import DatabaseCard from 'Component/DatabaseCard';
import AboutCard from 'Component/AboutCard';
import MenuCard from 'Component/MenuCard';
import { explosions } from 'Component/DatabaseCard/explosions_data';

class MainPageComponent extends PureComponent {
  render() {
    return (
      <main>
        <AboutCard />
        <MenuCard />
        <CalculatorCard />
        <DatabaseCard explosions={explosions} isTitle />
      </main>
    );
  }
}

export default MainPageComponent;
